import { ManWithCalculator } from "assets/SVG";
import React from "react";
import cssVar from "views/mobileView/styles";
const SingleContentIconCard = ({
  component,
  navigation,
}) => {
  const slide = component.content[0];
  let backgroundColor = '';
  let buttonColor = '';
  switch (slide.ColorScheme) {
    case "BLUE": {
      backgroundColor = "#F3F2FA"
      buttonColor = "#294C88"
      break;
    }
    case "GREEN": {
      backgroundColor = "#C5EED140"
      buttonColor = "#00A6A4"
      break;
    }
    case "YELLOW": {
      backgroundColor = "#FAF8F5"
      buttonColor = "#294C88"
      break;
    }
    case "PINK": {
      backgroundColor = "#F8EBEC"
      buttonColor = "#294C88"
      break;
    }
    case "LIGHTBLUE": {
      backgroundColor = "#F4F8FF"
      buttonColor = "#294C88"
      break;
    }
  }
  return (

    <div
      style={{
        padding: "4%",
        backgroundColor: backgroundColor,
        width: "100%"
      }}
    >
      <div style={{ color: "#333333", fontSize: "16px", fontWeight: "500", marginBottom: "4%" }}>
        {slide.SubTitle}
      </div>
      <p style={{ fontSize: cssVar.getPropertyValue('--font-helper'), fontWeight: "400", color: cssVar.getPropertyValue("--textLight"), marginBottom: 0, width: "90%" }}>
        {slide.Description}
      </p>
      <div style={{ display: "flex" }}>
        <div style={{ flex: 7, display: "flex", alignItems: "flex-end" }}>
          <button
            style={{
              borderWidth: "1px",
              borderRadius: 4,
              borderColor: buttonColor,
              fontSize: "14px",
              backgroundColor: slide.IsBorderedButton ? "#FFFFFF" : buttonColor,
              color: slide.IsBorderedButton ? buttonColor : "#FFFFFF",
              width: "95%",
              height: "44px",
            }}
            onClick={() => { navigation.push(slide.ActionTarget) }}
          >
            {slide.ActionText}
          </button>
        </div>
        <div
          style={{
            flex: 3,
            display: "flex",
            alignItems: "flex-end",
          }}
        >
          <img src={ManWithCalculator} alt="" />
        </div>
      </div>
    </div>
  );
};
export default SingleContentIconCard;
