import React from 'react'
import cssVar from "views/mobileView/styles";

export default function ReadMoreText({ text }) {

  const [isReadMore, setIsReadMore] = React.useState(true);

  if (text.length < 100) {
    return text
  }
  else return <>
    {isReadMore ? text.slice(0, 90) + "..." : text}
    <span onClick={() => setIsReadMore(!isReadMore)} style={{ color: cssVar.getPropertyValue('--brandSecondary') }}>
      {isReadMore ? " Read More" : " Show Less"}
    </span>
  </>
}